    data: {
      type: Object as () => KnowledgeBaseMatrix,
      default: Object as () => KnowledgeBaseMatrix
    },
    name: {
      type: String,
      default: ""
    }
  },

  data() {
    return {
      m: ["M1", "M2", "M3", "M4"]
    };
  },

  methods: {
    ...mapActions({
      setValueToKnowledgeBaseMatrix: "settings/setValueToKnowledgeBaseMatrix"
    })
  }
});
